#goTo button {
  background: linear-gradient(269deg, #0048ff, #0096ff);
  border-radius: 30px;
  outline: none;
  border: none;
  text-align: center;
  width: auto;
  letter-spacing: 0.03em;
  font-size: 0.75em;
  box-shadow: none;
  font-weight: 600;
  color: #fff;
  height: 43px;
  padding: 5px 24px;
  margin-right: 22px;
