  opacity: 0.9;
  letter-spacing: 0.9px;
}

#shape {
  position: absolute;
  font-size: 39px;
  bottom: 93%;
  right: 87px;
  color: #fff;
  text-shadow: 0 1px 8px rgb(0 0 0 / 50%);
  overflow: hidden;
  z-index: -1;
}

.Invisible {
  height: 100vh;
  left: 0;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 0;
}

.Cwrapper {
  position: absolute;
  top: 85px;
  left: 265px;
  z-index: 10;
  display: none;
  width: 250px;
  box-shadow: 0 1px 8px rgb(0 0 0 / 50%);
  border-radius: 3px;
}
.display {
  display: block;
}

/* Loader */
.spinner {
  margin: 20px auto;
  width: 50px;
  height: 30px;
  text-align: center;
  font-size: 10px;
}

.spinner > div {
  height: 100%;
  width: 6px;
  display: inline-block;
  margin-right: 3px;

  -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
  animation: sk-stretchdelay 1.2s infinite ease-in-out;
}

.rect3 {
  background-color: #0048ff;
}
.rect2 {
  background-color: #4076ff;
}

.rect1 {
  background-color: #759cff;
}

.spinner .rect2 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}

.spinner .rect3 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

@-webkit-keyframes sk-stretchdelay {
  0%,
  40%,
  100% {
    -webkit-transform: scaleY(0.4);
  }
  20% {
    -webkit-transform: scaleY(1);
  }
}

@keyframes sk-stretchdelay {
  0%,
  40%,
  100% {
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }
  20% {
    transform: scaleY(1);
    -webkit-transform: scaleY(1);
  }
}

